$TagW:60px;
.TagStyle{
    width: $TagW;
    text-align: center;
}
.Input_style{
    background-color: #fff;
    padding-left: 10px;
    border-radius: 6px;
}
.tab_view{
    margin-top: 10px;
}
.adm-list-item{
    padding: 0px;
}
.adm-list-default .adm-list-body{
    border-top: none;
    font-size: 14px;
}
.adm-input-element{
    font-size: 14px;
}
.adm-form-item.adm-form-item-horizontal.adm-list-item{
    --prefix-width: 2em;
}
.adm-card-body{
    padding: 0;
}
.btn_style{
    margin-top: 10em;
}
.btn_style button{
    width: 100%;
}
.info_style>p{
    margin-top: 8px;
    font-size: 14px;
}
.info_style>p:last-of-type{
    padding-bottom: 8px;
}
.adm-list-item-content{
    align-items: center;
}
.adm-list-item-content-prefix{
    width: auto;
}